	module.exports = {"data":{"markdownRemark":{"html":"<h2>Converting Between Radians and Degrees</h2>\n<h3>General Formulae</h3>\n<p>The following formulae can be used to convert between radians and degrees:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>r = d * (π / 180)\n\nd = r * (180 / π)\n\nwhere r is the angle in radians and d is the angle in degrees.</code></pre>\n      </div>\n<h3>Examples</h3>\n<p>Convert 2π/3 radians to degrees:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>2π/3 * 180/π = 360π/3π = 360/3 = 120 degrees</code></pre>\n      </div>\n<p>Convert 30 degrees to radians:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>30 * π/180 = 30π/180 = π/6 or (1/6)π radians\n\nNote: Radians are usually expressed in terms of π instead of a rounded decimal as this form represents the actual value rather than an approximation.</code></pre>\n      </div>","fields":{"slug":"/mathematics/converting-between-radians-and-degrees/"},"frontmatter":{"title":"Converting Between Radians and Degrees"}}},"pathContext":{"slug":"/mathematics/converting-between-radians-and-degrees/"}}

